body {
  --header-height: 60px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

:root {
  --blue-color: #00acdc;
}

header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-inline: 50px;
  display: flex;
}

a.is-active {
  color: var(--blue-color);
  position: relative;
}

ul {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

a {
  color: #000;
  height: var(--header-height);
  text-transform: uppercase;
  align-items: center;
  display: flex;
}

a:link {
  text-decoration: none;
}

a:hover {
  color: var(--blue-color);
}

a.is-active:after {
  content: "";
  background: var(--blue-color);
  border-radius: 2px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}
